'use client';

import QuestionItem from '@/components/custom/QuestionItem'; // Assuming QuestionItem is now in components/custom
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { useAuth } from '@/context/AuthContext';
import { useDebounce } from '@/lib/hooks';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { Category } from '../../types.d';

export default function QuestionsPage() {
  const { isAuthenticated, isLoading: isAuthLoading, user, logout } = useAuth();
  const router = useRouter();

  // All the state and logic from your original page.tsx
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [difficulty, setDifficulty] = useState('all');
  const [page, setPage] = useState(1);
  const [pagination, setPagination] = useState({ page: 1, totalPages: 1 });
  const debouncedSearchTerm = useDebounce(searchTerm, 300);

  useEffect(() => {
    // This is the protection logic. If not authenticated after check, redirect.
    if (!isAuthLoading && !isAuthenticated) {
      router.push('/login');
    }
  }, [isAuthenticated, isAuthLoading, router]);

  useEffect(() => {
    if (!isAuthenticated) return; // Don't fetch data if not authenticated

    const fetchData = async () => {
      setLoading(true);
      const params = new URLSearchParams({
        page: page.toString(),
        limit: '10',
        ...(debouncedSearchTerm && { search: debouncedSearchTerm }),
        ...(difficulty !== 'all' && { difficulty }),
      });

      try {
        const response = await fetch(
          `http://localhost:5000/api/v1/content?${params.toString()}`
        );
        if (!response.ok) throw new Error('Failed to fetch data');
        const result = await response.json();
        setCategories(result.data);
        setPagination(result.pagination);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : 'An unknown error occurred'
        );
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [debouncedSearchTerm, difficulty, page, isAuthenticated]);

  // While checking auth, render a loading state to prevent content flashing
  if (isAuthLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        Loading...
      </div>
    );
  }

  // After check, if not authenticated, render nothing (will be redirected)
  if (!isAuthenticated) {
    return null;
  }

  return (
    <main className="min-h-screen bg-background text-foreground p-4 sm:p-8">
      <div className="max-w-4xl mx-auto">
        <header className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-3xl font-bold">GeekHeaven DSA</h1>
            <p className="text-muted-foreground">Welcome, {user?.name}!</p>
          </div>
          <Button variant="outline" onClick={logout}>
            Logout
          </Button>
        </header>

        {/* Filter Bar, Accordion, and Pagination logic remains the same */}
        <Card className="mb-8">
          {/* ... your filter and search inputs ... */}
        </Card>

        {loading ? (
          <div>Loading questions...</div>
        ) : (
          <Accordion type="single" collapsible className="w-full">
            {categories.map((category) => (
              <AccordionItem value={category._id} key={category._id}>
                <AccordionTrigger>{category.title}</AccordionTrigger>
                <AccordionContent>
                  <div className="space-y-3">
                    {category.questions.map((question) => (
                      <QuestionItem key={question._id} question={question} />
                    ))}
                  </div>
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        )}

        {/* ... your pagination controls ... */}
      </div>
    </main>
  );
}
